import * as React from 'react';
import styled from 'styled-components';
import axios from 'axios';
import { NextPage } from 'next';

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { RangeStatic } from 'quill'

interface IEditor {
    htmlStr: string;
    setHtmlStr: React.Dispatch<React.SetStateAction<string>>;
}

const QuillEditor: NextPage<IEditor> = ({ htmlStr, setHtmlStr}) => {

    const quillRef = React.useRef<ReactQuill>(null);

    //   , modules     
    const imageHandler = () => {
        // file input  
        const input = document.createElement('input');
        input.setAttribute('type', 'file');
        input.click();

        input.onchange = async() => {
            const file = input.files;
            const formData = new FormData();

            if(file) {
                formData.append("multipartFiles", file[0]);
            }

            // file      
            const res = await axios.post('http://localhost:3005/manager/shop/save', formData);

            if(quillRef.current) {
                //  Editor       url    
                const index = (quillRef.current.getEditor().getSelection() as RangeStatic).index;

                const quillEditor = quillRef.current.getEditor();
                quillEditor.setSelection(index, 1);

                quillEditor.clipboard.dangerouslyPasteHTML(
                    index,
                    `<img src=${res.data} alt=${'alt text'} />`
                );
            }
        }
    }

    // useMemo   handler     focus 
    const modules = React.useMemo(() => ({
            toolbar: {
                // container   tool 
                container: [
                    [{ 'font': [] }], // font 
                    [{ 'header': [1, 2, 3, 4, 5, 6, false] }], // header 
                    ['bold', 'italic', 'underline','strike', 'blockquote', 'code-block', 'formula'], // , ,    tool 
                    [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}], // ,  
                    ['link', 'image', 'video'], // , ,   
                    [{ 'align': [] }, { 'color': [] }, { 'background': [] }], // ,  ,   
                    ['clean'], // toolbar   
                ],

                // custom  
                handlers: {
                    image: imageHandler, //  tool    
                }
            },
        }
    ), [])

    // toolbar  tool format
    const formats = [
        'font',
        'header',
        'bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block', 'formula',
        'list', 'bullet', 'indent',
        'link', 'image', 'video',
        'align', 'color', 'background',
    ]

    return (
        <CustomReactQuill
            ref={quillRef}
            theme="snow"
            modules={modules}
            formats={formats}
            value={htmlStr}
            placeholder=' .'
            onChange={(content, delta, source, editor) => setHtmlStr(editor.getHTML())} />
    )
}

export default QuillEditor;

// style
const CustomReactQuill = styled(ReactQuill)`
    height: 300px;
`