import React, { useState } from 'react';
import {Login} from "../Styles/Loginform/Login.style";
import { useNavigate, useLocation } from 'react-router-dom';
import axios from '../Request/RequestConfig.js';


const LoginPage = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loginError, setLoginError] = useState('');

    const navigate = useNavigate();
    const location = useLocation();


    const handleLogin = async () => {
        try {
            const response = await axios.post('/login', JSON.stringify({ email, password }), {
                headers: {
                    'Content-Type': 'application/json',
                },
            });

            if (response.status == 200) {
                //     
                const accessToken = response.headers.get('access-token');
                const refreshToken = response.headers.get('refresh-token');

                console.log(accessToken);

                const data = await response.data;
                console.log(JSON.stringify(data)); // JSON  String 
                console.log(data.data.username);



                //   
                sessionStorage.setItem('userData', JSON.stringify(data.data));

                //     localStorage 
                localStorage.setItem('accessToken', accessToken);
                localStorage.setItem('refreshToken', refreshToken);

                //gamja@gmail.com
                navigate('/shop');

            } else {
                //     
                setLoginError(' .  .');
            }
        } catch (error) {
            console.error('    .', error);
            setLoginError('    .');
        }
    };

    const handleJoin = () => {
        navigate('/join')
    }


    return (
        <Login>
            <input
                type="email"
                placeholder="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
            />
            <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
            />
            <button onClick={handleLogin}></button>
            <button onClick={handleJoin}></button>
        </Login>
    );
}

export default LoginPage;
