import React, {useState, useEffect } from 'react';
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import {useNavigate, useLocation } from 'react-router-dom';




const UserHeader = () => {
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [userName, setUserName] = useState('');
    const userDataString = sessionStorage.getItem('userData');
    const userData = userDataString ? JSON.parse(userDataString) : null;

    const navigate = useNavigate();
    const location = useLocation();


    //    
    const handleLogin = () => {
        navigate('/login');
    };

    //    
    const handleLogout = () => {
        setIsLoggedIn(false);
        setUserName('');
        sessionStorage.removeItem('userData');
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
    };

    useEffect(() => {
        if (userData) {
            setIsLoggedIn(true);
            setUserName(userData.username);

        }
    }, [userData]);





    return (<header>
        {isLoggedIn ? (<Card style={{width: '300px', height: '100px'}}>
            <Card.Header style={{fontSize: '16px', padding: '2px'}}></Card.Header>
            <Card.Body style={{fontSize: '14px', padding: '2px'}}>
                <Card.Title style={{fontSize: '18px', marginBottom: '8px'}}>, { userName } </Card.Title>
                <Button variant="primary" size="sm" onClick={handleLogout}>  </Button>
                <Button variant="primary" size="sm" style={{marginLeft: '30px'}}>    </Button>
            </Card.Body>
        </Card>) : (<Card>
            <Button onClick={handleLogin}></Button>
        </Card>)}
    </header>);
};

export default UserHeader;
